import { Grid, TextField, Typography } from '@material-ui/core'
import React from 'react'
import CustomTimeline, { CustomTimelineSeparator } from '../../components/Timeline/Timeline'

import Resumedata from '../../utitls/Resumedata'
import './Resume.css'
import WorkIcon from '@material-ui/icons/Work'
import SchooolIcon from '@material-ui/icons/School'
import TimelineItem from '@material-ui/lab/TimelineItem'
import TimelineConnector from '@material-ui/lab/TimelineConnector';
import TimelineContent from '@material-ui/lab/TimelineContent';
import Paper from '@material-ui/core/Paper'
import TimelineDot from '@material-ui/lab/TimelineDot';

import TimelineSeparator from '@material-ui/lab/TimelineSeparator'
import CustomButton from '../../components/Button/Button'

const Resume = () => {
    return (
        <div>
            {/*About me*/}
            <Grid container className="section pb_45 pt_45">
                <Grid item className="section_title mb_30" >
                    <span></span>
                    <h6 className="section_title_text">About me</h6>
                </Grid>
                <Grid item xs={12}>
                    <Typography variant='body2' className="aboutme_text">{Resumedata.about}</Typography>
                </Grid>
            </Grid>
            {/*Education and exeperience */}
            <Grid container className="section pb_45">
                <Grid item className="section_title mb_30" >
                    <span></span>
                    <h6 className="section_title_text">Resume</h6>
                </Grid>
                <Grid item xs={12}>
                    <Grid container className="resume_timeline">
                        {/* exeperience */}
                        <Grid item sm={12} md={6}>
                            <CustomTimeline title="Work experience"
                                icon={<WorkIcon />}>
                                {Resumedata.experiences.map((experience) => (
                                    <TimelineItem>
                                        <CustomTimelineSeparator />
                                        <TimelineContent className="timeline_content">
                                            <Typography className="timeline_title">{experience.title}</Typography>
                                            <Typography variant='caption' className="timeline_date">{experience.date}</Typography>
                                            <Typography variant='body2' className="timeline_discription">{experience.discription}</Typography>
                                            <Typography className="timeline_company">{experience.company}</Typography>
                                        </TimelineContent>
                                    </TimelineItem>
                                ))}
                            </CustomTimeline>

                        </Grid>
                        {/*Education */}
                        <Grid item sm={12} md={6}>
                            <CustomTimeline title="Education"
                                icon={<SchooolIcon />}>
                                {Resumedata.educations.map((education) => (
                                    <TimelineItem>
                                        <CustomTimelineSeparator />
                                        <TimelineContent className="timeline_content">
                                            <Typography className="timeline_title">{education.title}</Typography>
                                            <Typography variant='caption' className="timeline_date">{education.date}</Typography>
                                            <Typography variant='body2' className="timeline_discription">{education.institute}</Typography>
                                            <Typography className="timeline_company">{education.Percentage}</Typography>
                                        </TimelineContent>
                                    </TimelineItem>
                                ))}
                            </CustomTimeline>

                        </Grid>
                    </Grid>
                </Grid>

            </Grid>
            {/*services */}
            <Grid container className="section pb_45">
                <Grid item className="section_title mb_30" >
                    <span></span>
                    <h6 className="section_title_text">My services</h6>
                </Grid>
                <Grid item xs={12}>
                    <Grid container spacing={3} justify='space-between'>
                        {Resumedata.services.map((service) => (
                            <Grid item xs={12} sm={6} md={3}>
                                <div className="service">
                                    <Typography className="service_title" variant="h6">{service.title}</Typography>
                                    <Typography className="service_detail" variant="body2">{service.detail}</Typography>
                                </div>
                            </Grid>
                        ))}
                    </Grid>
                </Grid>
            </Grid>


            {/*Skills*/}
            <Grid container className="section graybg pb_45 p_50">
            <Grid item className="section_title mb_30" >
            <span></span>
            <h6 className="section_title_text">Skills</h6>
        </Grid>
                <Grid item xs={12}>
                    <Grid container justify='space-between' spacing={3}>
                        {Resumedata.skills.map((skill) => (
                            <Grid item xs={12} sm={6} md={3}>
                                <Paper elevation={0} className="skill">
                                    <Typography variant='h6' className="skill_title">
                                        {skill.title}
                                    </Typography>
                                    {skill.detail.map((element) => (
                                        <Typography variant='body2' className="skill_detail">
                                            <TimelineDot variant={'outlined'} className='timeline_dot' />
                                            {element}
                                        </Typography>
                                    ))}
                                </Paper>
                            </Grid>
                        ))}
                    </Grid>
                </Grid>

            </Grid>
            {/*Contact*/}
            <Grid container spacing={5} className="section pt_45 pb_45">
                {/*contact form*/}
                <Grid item xs={12} lg={7}>

                    <Grid item className="section_title mb_30">
                        <span></span>
                        <h6 className="section_title_text">Contact Form</h6>
                    </Grid>

                    <Grid item xs={12}>
                        <Grid container spacing={2} >
                            <Grid item xs={12} sm={6}>
                                <TextField fullWidth name='name' label='name'></TextField>
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <TextField fullWidth name='email' label='Email'></TextField>
                            </Grid>
                            <Grid item xs={12} >
                                <TextField fullWidth name='Message' label='Message' multiline rows={3}></TextField>
                            </Grid>
                            <Grid item xs={12}>
                                <CustomButton text='Submit'/>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>


                {/*contact Information*/}
                <Grid item xs={12} lg={5}>
                    <Grid container>

                        <Grid item className="section_title mb_30">
                            <span></span>
                            <h6 className="section_title_text">Contact Information</h6>
                        </Grid>
                        <Grid item xs={12} >
                            <Grid container spacing={2}>
                                <Grid item xs={12}>
                                    <Typography className="contactInfo_item">
                                        <span>Address:</span>{Resumedata.address}
                                    </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <Typography className="contactInfo_item">
                                        <span>Phone:</span>{Resumedata.phone}
                                    </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <Typography className="contactInfo_item">
                                        <span>Email:</span>{Resumedata.email}
                                    </Typography>
                                </Grid>
                            </Grid>
                        </Grid>
                        <Grid item xs={12}>
                        <Grid container className='contactInfo_socialscontainer'>
                        {Object.keys(Resumedata.socials).map((key)=>(
                            <Grid item>
                            <a href={Resumedata.socials[key].link}>{Resumedata.socials[key].Icon}</a></Grid>
                        ))}
                        </Grid>
                        
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </div>
    )
}

export default Resume
