import { Typography } from '@material-ui/core'
import React from 'react'
import './Profile.css'
import Resumedata from '../../utitls/Resumedata'
import PersonOutlinedOutlinedIcon from '@material-ui/icons/PersonOutlineOutlined'
import CustomTimeline, { CustomTimelineSeparator } from '../Timeline/Timeline'
import TimelineItem from '@material-ui/lab/TimelineItem'
import TimelineContent from '@material-ui/lab/TimelineContent'
import CustomButton from '../Button/Button'
import GetAppIcon from '@material-ui/icons/GetApp'


const CustomTimelineItem = ({ title, text, link }) => (
    <TimelineItem>
        <CustomTimelineSeparator />
        <TimelineContent className="timeline_content">
          {link ? (<Typography className="timelineItem_text">
                <span>{title}:</span>
                <a href={link} target="_blank">
                    {text}
                </a>
            </Typography>
            ) : (
                <Typography className="timelineItem_text">
                <span>{title}:</span>
                    {text}
                </Typography>
            )}
        </TimelineContent>
    </TimelineItem>
)
export default function Profile() {
    return (
        <div className='profile container_shadow'>
            <div className='profile_name'>
                <Typography className='name'>{Resumedata.name}</Typography>
                <Typography className='title'>{Resumedata.title}</Typography>
            </div>
            <figure className='profile_image'>
                <img src={require('../../assests/Images/abc.jpg').default} alt='' />
            </figure>

            <div className='profile_information'>
                <CustomTimeline Icon={<PersonOutlinedOutlinedIcon />} >
                 <CustomTimelineItem title="Name" text={Resumedata.name}/>
                <CustomTimelineItem title='Birthday' text={Resumedata.birthday}/>
                <CustomTimelineItem title='Email' text={Resumedata.email}/>
                <CustomTimelineItem title='Phone' text={Resumedata.phone}/>
            
                </CustomTimeline>
               <div className="button_container">
               <a href="ResumeK.pdf" style={{textDecoration:"none"}} download='ResumeK.pdf'>
               <CustomButton text={"Download CV"} icon={<GetAppIcon/>}/></a>
               </div>
                
            </div>

        </div>
    )
}
