import React from 'react'
import { Button, Form, FormControl, Nav, Navbar, NavDropdown } from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'
import {Link,NavLink,withRouter} from 'react-router-dom'
import {HomeRounded,SchoolRounded,WorkRounded,Facebook,Twitter,LinkedIn,GitHub,Telegram} from '@material-ui/icons'
import Resumedata from '../../utitls/Resumedata'
import Custombutton from '../Button/Button'
import './Header.css'


const Header = (props) => {
    const pathName=props?.location?.pathname;
    return (
        <Navbar expand="lg" sticky="top" className="header">
        <Nav.Link as={NavLink} to="/" classNane="header_navlink">
        <Navbar.Brand className='header_home'>
        <HomeRounded/>
        </Navbar.Brand>
        </Nav.Link>
        <Navbar.Toggle/>
        <Navbar.Collapse>
        <Nav className="header_left">
        <Nav.Link as={NavLink} 
        to="/"
         className={pathName=='/' ? 'header_link_active':'header_link'}>Resume</Nav.Link>

        <Nav.Link as={NavLink} 
        to="/Portfolio"
         className={pathName=='/Portfolio' ? 'header_link_active':'header_link'}>Portfolio</Nav.Link>
        </Nav>
        <div className="header_right">
        {Object.keys(Resumedata.socials).map((key)=>(
            <a href={Resumedata.socials[key].link} target="_blank">{Resumedata.socials[key].Icon}</a>
        ))}
        <a href='https://t.me/Kapil16j' style={{textDecoration:"none"}}><Custombutton text={'Hire me'} icon={<Telegram/>}/></a>
        </div>
        </Navbar.Collapse>
        </Navbar>
        
    )
}

export default withRouter(Header)
